import Layout from '@/layout'

const codeRouter = {
  path: '/charts',
  component: Layout,
  redirect: '/charts/bar-charts',
  name: 'Charts',
  meta: { title: '', icon: 'el-icon-s-data', roles: 'admin' },
  children: [
    {
      path: 'pictorial-bar',
      component: () => import('@/views/charts/pictorial-bar'),
      name: 'PictorialBar',
      meta: { title: '' }
    },
    {
      path: 'bar-charts',
      component: () => import('@/views/charts/bar-charts'),
      name: 'BarCharts',
      meta: { title: '' }
    },
  ]
}

export default codeRouter
