import Layout from '@/layout'

const codeRouter = {
  path: '/code',
  component: Layout,
  redirect: '/code/qr-code',
  name: 'Code',
  meta: { title: '', icon: 'el-icon-postcard', roles: 'admin' },
  children: [
    {
      path: 'qr-code',
      component: () => import('@/views/code/qr-code'),
      name: 'QrCode',
      meta: { title: '' }
    },
    {
      path: 'bar-code',
      component: () => import('@/views/code/bar-code'),
      name: 'BarCode',
      meta: { title: '' }
    }
  ]
}

export default codeRouter
