import Vue from 'vue'
import copy from './copy' // 
import debounce from './debounce' // 
import waterMarker from './waterMarker' // 
import draggable from './draggable' // 

const directives = {
  copy,
  debounce,
  waterMarker,
  draggable
}

Object.keys(directives).forEach((key) => {
  Vue.directive(key, directives[key])
})
